@use "./mixin" as m;

#root {
  section.menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
    h1 {
      margin-bottom: 60px;
    }
    form.theme {
      legend {
        margin-bottom: 15px;
        font-weight: 800;
        font-size: 0.8em;
        text-align: center;
      }
      div {
        display: flex;
        width: 180px;
        height: 30px;
        border: 1px solid rgb(255, 255, 255, 0.1);
        border-radius: 30px;
        overflow: hidden; /////???????
        box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
        input {
          display: none;
          &:checked + label::before {
            display: inline-block;
          }
        }
        label {
          position: relative;
          flex: 1;
          cursor: pointer;
          opacity: 0.3;
          &:hover {
            opacity: 1;
          }
          @include m.icon(before, "\f00c");
          &[for="radio1"] {
            background-color: #ccc;
          }
          &[for="radio2"] {
            background-color: skyblue;
          }
          &[for="radio3"] {
            background-color: bisque;
          }
          &::before {
            display: none;
            @include m.posLT(absolute, 50%, 50%, -50%, -50%);
            color: #fff;
          }
        }
      }
    }
    nav {
      margin-top: 45px;
      font-weight: 900;
      text-transform: uppercase;

      a {
        display: block;
        margin: 20px 0;
        padding: 7px 20px;
        text-align: center;
        border-radius: 20px;
        box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
        &:hover {
          text-shadow: -2px -2px 1px rgba(255, 255, 255, 1), 2px 2px 1px rgba(0, 0, 0, 0.1);
        }
        &:active {
          box-shadow: -2px -2px 5px rgba(0, 0, 0, 0.2), 3px 3px 5px rgba(255, 255, 255, 1);
        }//가상선택자의 일종. 누르고있는동안
        i {
        }
      }
    }
  }
} //root
